import styled from "styled-components";

export const FooterStyled = styled.footer`
    width: 100%;
    background: #242323;
    padding-top: 70px;
    margin-top: 50px;
    align-self: flex-end;
    @media only screen and (max-width: 1023px) {
        padding-top: 0;
    }
`

export const FooterAbout = styled.p`
    color: #b7b7b7;
    margin-bottom: 60px;
    padding: 0 50px;
    text-align: center;
    @media only screen and (max-width: 1023px) {
        display: none;
        margin-bottom: 0;
        padding: 0;
    }
`

export const FooterCopyright = styled.div`
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    padding: 20px 0;
    margin-top: 40px;
    text-align: center;   
    p{
        color: #b7b7b7;
        margin-bottom: 0;
    }
    p i{
        color: #e53637;
    }
    p a{
        color: #e53637;
    }
    @media only screen and (max-width: 414px) {
        font-size: 11px;
    }
`