import React from 'react'
// import { Card } from 'react-bootstrap'
import { AreaFeaturedStyled } from './styled'

const AreaFeatured = ({ data }) => {
    return (
        <AreaFeaturedStyled to={"/hsr/search/area/" + data.ID_AREA}>
            <img src={data.ROUTE_IMAGE} alt={data.DESCRIPTION_AREA}/>
            <a href={"/#/hsr/search/area/" + data.ID_AREA}>
                <h6> {data.DESCRIPTION_AREA} </h6>
            </a>
            <p className="counter"> {data.COUNT_ARTICLES + " articulos"}</p>
        </AreaFeaturedStyled>
    )
}

export default AreaFeatured
