import { useEffect } from "react";
import Search from "components/molecules/Search/Search";
import useAxios from "hooks/useAxios";

const Searcharticlesafeatured = () => {
  const articlesfeatured = useAxios("GET", "/", null, false);

  useEffect(() => {
    articlesfeatured.updateURL(`/articulos/destacados`);
    articlesfeatured.initRequest();
    return () => {};

  }, []);
  return (
    <div>
      <Search
        data={articlesfeatured.data}
      />
    </div>
  );
};

export default Searcharticlesafeatured;
