import AreaFeatured from "components/molecules/AreaFeatured/AreaFeatured";
import TitleSection from "components/atoms/TitleSection";
import { AreasFeaturedStyled } from "./styled";
import useAxios from "hooks/useAxios";

const AreasFeatured = () => {
  const areasFeatured = useAxios("GET", "/areas/destacadas");
  return (
    <div>
      <TitleSection href="/#/hsr/get/areasfeatured">Areas Destacadas</TitleSection>
      <AreasFeaturedStyled>
        {areasFeatured.data.map((data) => (
          <AreaFeatured data={data} />
        ))}
      </AreasFeaturedStyled>
    </div>

  );
};

export default AreasFeatured;
