import React, { Fragment, useEffect, useState } from "react";
import { Select, Form } from "react-bootstrap";
import DataTable from "react-data-table-component";
import useAxios from "hooks/useAxios";
 import { NoData, SearchStyled } from "./styled";



const GetTheme = () => {
   
  const data_theme = useAxios("GET", "/articulo/temas/listar")
  console.log("los temas son los siguientes "+data_theme);
  
return (
  <Fragment>

    <SearchStyled> 
      <DataTable
        noDataComponent={<NoData><span>No se encontró ningun resultados.</span></NoData>}
        columns={[
          {
            name: "ID", cell: (el) =>
              <el className="data category">{el.ID_THEME}</el>
          }
          ,
          {
            name: "CATEGORIA", cell: (el) =>
              <el className="data categoria">{el.DESCRIPTION_THEME}</el>

          },
        
        ]}
        pagination
        data={data_theme.data}   
      />
     </SearchStyled> 
  </Fragment>

);

}
export default GetTheme;