import React, { Fragment, useEffect, useState } from "react";
import { Select, Form } from "react-bootstrap";
import DataTable from "react-data-table-component";
import useAxios from "hooks/useAxios";
 import { NoData, SearchStyled } from "./styled";



const GetAreas = () => {
 
  const data_areas = useAxios("GET", "/areas/listar")
  console.log("la data de areas es "+ data_areas );
  
return (
  <Fragment>

    <SearchStyled> 
      <DataTable
        noDataComponent={<NoData><span>No se encontró ningun resultados.</span></NoData>}
        columns={[
          {
            name: "ID", cell: (el) =>
              <el className="data NOMBRE">{el.ID_AREA}</el>
          }
          ,
          {
            name: "DESCRIPCION ARTICULO", cell: (el) =>
              <el className="data NOMBRE">{el.DESCRIPTION_AREA}</el>

          },
          {
            name: "AREA DESTACADA", cell: (el) =>
              <el className="data NOMBRE">{el.STATE_AREA_FEATURED}</el>

          },
          {
            name: "IMAGEN", cell: (el) =>
              <el className="data NOMBRE">
               <img style={{ width: "40%" }} src={el.ROUTE_IMAGE} alt={el.ROUTE_IMAGE} />
                </el>
          }
          ,
        ]}
        pagination
        data={data_areas.data}
      />
     </SearchStyled> 
  </Fragment>

);

}
export default GetAreas;