import { LastStyled } from "./styled"
import { Button } from 'react-bootstrap';
import { useHistory } from 'react-router'

const Last = ({ data }) => {
    const { push } = useHistory()
    return (

        <LastStyled>
            <h5 >{data.TITLE_ARTICLE}</h5>
            <div className="content-last-article">
                <div>
                    <p className="p-featured-last">
                        {data.CONTENT_ARTICLE}
                    </p>
                </div>
                <div className="content-last-article-img-2">
                    <img
                        src={data.ROUTE_IMG}
                        alt={data.TITLE_ARTICLE}
                    />
                    <Button className="btn-article-last" onClick={() => push('/hsr/article/' + data.ID_ARTICLE)}>
                        Ver descripción
                    </Button>
                </div>
            </div>
        </LastStyled>
    )
}

export default Last
