import { useEffect } from "react";
import Search from "components/molecules/Search/Search";
import useAxios from "hooks/useAxios";

const Searcharticlesaddrecently = () => {
  const articlesaddrecently = useAxios("GET", "/", null, false);

  useEffect(() => {
    articlesaddrecently.updateURL(`/articulos/recientes`);
    articlesaddrecently.initRequest();
    return () => {};
    //eslint-disable-next-line
  }, []);
  return (
    <div>
      <Search
        data={articlesaddrecently.data}
        mensaje="Hola mundo"
      />
    </div>
  );
};

export default Searcharticlesaddrecently;
