import React, { Fragment, useEffect, useState } from "react";
import { Button } from 'react-bootstrap';
import DataTable from "react-data-table-component";
import useAxios from "hooks/useAxios";
import { NoData, SearchStyled } from "./styled";
import { faHome } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import AdminArticle from "pages/administrators/admin_article";
import Formulario from "components/organismis/Formulario";
import ModalsArticle from "pages/Modalsframes/modalsArticle.js"
import Modal from "pages/Modal.js";
import { compareAsc, format } from 'date-fns'


const GetArticles = () => {
  const [show, setShow] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [showArticleedit, setShowArticleedit] = useState(false);
  const [dataForm, setDataForm] = useState({})

  const data_articles = useAxios("GET", "/articulos/listar")
  const API_DELETEARTICLE = useAxios("POST", "/articulos/deleteArticle", null, false);

  useEffect(() => {
    data_articles.sendRequest();
    return () => {}
  }, [show])

  const addArticle = () => {
    setShow(true);
    setDataForm("")
  }

  const handleUpdate = (data) => {

    setShow(true);
    setDataForm(data.ID_ARTICLE)
    console.log(data.ID_ARTICLE);

    // setVisible(true)
  }

  const handleDelete = (data) => {

    // setShow(true);
    setShowModal(true)
    let id_article = data.ID_ARTICLE
    console.log(id_article);
    // setDataForm(data.ID_ARTICLE)
    API_DELETEARTICLE.updateURL(`/articulos/deleteArticle/${id_article}`);
    API_DELETEARTICLE.initRequest();
    console.log(data.ID_ARTICLE);

    // setVisible(true)
  }

  useEffect(() => {


  },[])


  return (
    <Fragment>

      <SearchStyled>
        <div className="btnAdd">
          <Button variant="primary" onClick={addArticle}>Agregar</Button>{' '}
        </div>

        <DataTable
          noDataComponent={<NoData><span>No se encontró ningun resultados.</span></NoData>}
          columns={[
            {
              name: "ID", cell: (el) =>
                <el className="data NOMBRE">{el.ID_ARTICLE}</el>
            }
            ,
            {
              name: "DESCRIPCION", cell: (el) =>
                <el className="data NOMBRE">{el.TITLE_ARTICLE}</el>

            },
            {
              name: "AUTOR", cell: (el) =>
                <el className="data NOMBRE">{el.AUTHOR}</el>
            }
            ,
            {
              name: "CONTENIDO", cell: (el) =>
                <el className="data NOMBRE">{el.CONTENT_ARTICLE.substring(0,100)}</el>

            },
            {
              name: "USUARIO CREA", cell: (el) =>
                <el className="data NOMBRE">{el.USER_CREATION}</el>
            }
            ,
            {
              name: "FECHA CREACIÓN", cell: (el) =>
                <el className="data NOMBRE">{format(new Date(el.DATE_CREATION_REG), 'dd-MM-yyyy')}</el>

            },
            {
              name: "IMAGEN", cell: (el) =>
                <el className="data NOMBRE"><img style={{ width: "100px" }} src={el.ROUTE_IMG} alt="" /></el>
            }
            ,
            {
              name: "ARCHIVO", cell: (el) =>
                <el className="data NOMBRE"><span>{el.ROUTE_FILE}</span></el>

            }
            ,
            {
              name: "DESTACADO", cell: (el) =>
                <el className="data NOMBRE"><span>{el.ARTICLE_FEATURED}</span></el>

            }
            , {
              name: "ACCIÓN", cell: (el) =>
                <div>
                  <Button className="btn-editart" type="button" onClick={() => handleUpdate(el)}>
                    Editar
                  </Button>
                  <Button className="btn-delart" type="button" onClick={() => handleDelete(el)}>
                    Eliminar
                  </Button>
                  {/* <ItemArticleTable data={el} /> */}
                </div>
            },

          ]}
          pagination
          data={data_articles.data.reverse()}
        />
      </SearchStyled>

      {/* <ModalsArticle show={show} setShow={setShow}  /> */}
      <ModalsArticle show={show} setShow={setShow} id_article={dataForm} />
      {!Array.isArray(API_DELETEARTICLE.data) && <Modal title="Confirmación" text={API_DELETEARTICLE.data} show={showModal} setShow={setShowModal} onHide={() => {}}>
      </Modal>}
    </Fragment>

  );

}
export default GetArticles;