import { Route } from "react-router-dom";
import Explorer_areas from "pages/explorer/explorer_areas.js";
import Explorer_fecha from "pages/explorer/explorer_fecha.js";
import Explorer_theme from "pages/explorer/explorer_theme.js";
import Explorer_type_article from "pages/explorer/explorer_type_article.js";
import Explorer_language from "pages/explorer/explorer_language.js";

function explorar() {
  return (
    <div>
      <Route exact path="/bhs/explorer/area" component={Explorer_areas} />
      <Route exact path="/bhs/explorer/date" component={Explorer_fecha} />
      <Route exact path="/bhs/explorer/theme" component={Explorer_theme} />
      <Route
        exact
        path="/bhs/explorer/typearticle"
        component={Explorer_type_article}
      />
      <Route
        exact
        path="/bhs/explorer/language"
        component={Explorer_language}
      />
    </div>
  );
}

export default explorar;
