import { ContentArticle, Styled } from "./styled"
import { Link } from 'react-router-dom';

const ItemArticleTable = ({ data }) => {
    return (
        <Styled>
            <Link className="Link-img" to={'/hsr/article/' + data.ID_ARTICLE}>
                <img src={data.ROUTE_IMG} alt={data.TITLE_ARTICLE} />
            </Link>
            <ContentArticle>
                <Link to={'/hsr/article/' + data.ID_ARTICLE}>
                    <span>{data.TITLE_ARTICLE}</span>
                </Link>
                <p className="span-content">{data.CONTENT_ARTICLE}</p>
            </ContentArticle>
        </Styled>
    )
}

export default ItemArticleTable
