import React, { Fragment, useEffect, useState } from "react";
import { Button, Modal } from "react-bootstrap";
import Admin_article from "../administrators/admin_article.js"



const ModalsArticle = ({ title, text, status, show, setShow, id_article }) => {

    const [fullscreen, setFullscreen] = useState(true);
    // console.log(id_article)

  return (
    <Fragment>
      <Modal show={show} fullscreen={fullscreen} onHide={() =>{setShow(false)}}>
        <Modal.Header closeButton>
          <Modal.Title>{title}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
           {/* {id_article? <Edit_article setModalprueba={setShow}  id_article={id_article}/>:<Admin_article setModalprueba={setShow}/> }  */}
         <Admin_article setModalprueba={setShow}  id_article={id_article} />
        </Modal.Body>
      </Modal>
      );
    </Fragment>
  );

}

export default ModalsArticle;