import { Link } from "react-router-dom";
import { Button } from "react-bootstrap";
import { Container } from "components/Styled";
import TitleSection from "components/atoms/TitleSection";
import ExplorerListArticles from "components/organismis/ExplorerListArticles";
import useAxios from "hooks/useAxios";

const ExplorerAreas = () => {

  const artbyarea = useAxios("GET", "/articulos/por/areas/listar");
  return (
    <Container>
      {artbyarea.data.map((articlesbyarea, key) => (
        <div key={key}>
          {articlesbyarea.ARTICLES.length > 0 && (
            <div>
              <TitleSection href={"/#/hsr/search/area/" + articlesbyarea.ID_AREA}>
                {articlesbyarea.DESCRIPTION_AREA}
              </TitleSection>
              <ExplorerListArticles articles={articlesbyarea.ARTICLES} />
              <div className="btn-explorer">
                <Button variant="dark" size="sm">
                  <Link to={"/hsr/search/area/" + articlesbyarea.ID_AREA}>
                    <span className="ver-mas">Ver mas</span>
                  </Link>
                </Button>
              </div>
            </div>
          )}
        </div>
      ))}
    </Container>
  );
};

export default ExplorerAreas;
