import { Link } from "react-router-dom";
import { AreaFeaturedStyled } from "../AreaFeatured/styled";

const Article = ({ data }) => {
    return (
        <AreaFeaturedStyled to={"/hsr/article/" + data.ID_ARTICLE}>
            <img src={data.ROUTE_IMG} alt="..."/> 
            <Link to={"/#/hsr/article/" + data.ID_ARTICLE}>
                <h6>{data.TITLE_ARTICLE}</h6>
            </Link>
        </AreaFeaturedStyled>
    )
}

export default Article
