import { useEffect } from "react";
import Search from "components/molecules/Search/Search";
import useAxios from "hooks/useAxios";

const Search_General = (props) => {
  const search_result = useAxios("GET", "/", null, false);

  useEffect(() => {
    var id_general = props.match.params.idgeneral;
    // console.log(id_general);
    search_result.updateURL(`/articulos/busqueda/${id_general}`);
    search_result.initRequest();
    return () => {};
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <div>
      <Search
        data={search_result.data}
        title_busqueda="Busqueda"
      />
    </div>
  );
};



export default Search_General;
