import React, { useState, useEffect, useMemo } from "react";
import Axios from "axios";
import useLocalStorage from "./useLocalStorage";
import { useHistory } from "react-router-dom";

const UsuarioContext = React.createContext();
export function UsuarioProvider(props) {
  const { push } = useHistory();
  const [initVal, setInitVal] = useState(true)
  const [usuario, setUsuario] = useState(null);
  const [cargandoUsuario, setCargandoUsuario] = useState(true);
  const [setLogin,getLogin] = useLocalStorage("BibliotecaUserLogin")
  useEffect(() => {
    recuperarUsuario();
    setInitVal(false)
    return () => {};
  //eslint-disable-next-line
  }, []);

  const recuperarUsuario = async (e) => {
    setCargandoUsuario(true);
    const store = getLogin();
    if (store && store.login) {
      await setUsuario(store);
    }else{
        push("/login")
    }
    setCargandoUsuario(false);
  };
  const login = async (username, password) => {
    setCargandoUsuario(true);
    try {
      const res = await Axios.post(`http://190.223.56.109:5000/login/`, {
        username: username,
        password: password,
      });
      const { token, user, idrol, idEmpleado, dni } = res.data;
      setLogin({
        login: true,
        store: token,
        login_username: user,
        rol: idrol,
        idEmpleado: idEmpleado,
        dni: dni,
      })
      recuperarUsuario();
      setCargandoUsuario(false);
      return [
        "AUTENTICACION CORRECTA",
        "Bienvenido al hospital " + user + "!",
        200,
      ];
    } catch (error) {
      setCargandoUsuario(false);
      return [
        "AUTENTICACION INCORRECTA",
        "VERIFIQUE SU USUARIO Y CONTRASEÑA",
        501,
      ];
    }
  };
  const logout = (e) => {
    localStorage.removeItem("BibliotecaUserLogin");
    sessionStorage.clear();
    setUsuario(null);
  };

  const value = useMemo(() => {
    return {
      usuario,
      cargandoUsuario,
      login,
      logout,
      initVal
    };
    //eslint-disable-next-line
  }, [usuario, cargandoUsuario,initVal]);
  return <UsuarioContext.Provider value={value} {...props} />;
}
export function useUsuario() {
  const context = React.useContext(UsuarioContext);
  if (!context) {
    throw new Error("No se encontro Usuario en el contexto");
  }
  return context;
}