import { Container, Navbar, NavDropdown, Nav } from "react-bootstrap";
import LOGOHSRP from "assets/img/img_navbar/LOGOHSRP.png";
import { useUsuario } from "hooks/ContextUser";
import { Logo, NavBarStyled } from "./styled";
import { useEffect, useState } from "react";

const NavbarX = () => {
  const { usuario, logout } = useUsuario();
  const [adminIsValid, setAdminIsValid] = useState(false);

  useEffect(() => {
    setAdminIsValid( usuario && (usuario.rol === 36 || usuario.rol === 9)  )
    return () => {}
  }, [usuario])
  return (
    <NavBarStyled collapseOnSelect expand="lg" bg="dark" variant="dark">
      <Container>
        <Logo to={{pathname: "/home"}} >
          <img id="LOGOHSRP" src={LOGOHSRP} alt="LOGO SANTA ROSA" />
          <div className="logohsr">
            <h6>BIBLIOTECA DIGITAL </h6>
            <h6>HOSPITAL DE LA AMISTAD PERU</h6>
            <h6>COREA SANTA ROSA II-2</h6>
          </div>
        </Logo>

        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto">

            <NavDropdown title="Nosotros" id="collasible-nav-dropdown">
              <NavDropdown.Item href="/#/hsr/about-us/about">
                Sobre Nosotros
              </NavDropdown.Item>
              <NavDropdown.Item href="/#/hsr/about-us/Organigrama">
                Organigrama
              </NavDropdown.Item>
              <NavDropdown.Item href="/#/hsr/about-us/teaching">
                Docencia
              </NavDropdown.Item>
              {/* <NavDropdown.Item href="/bhs/about-us/calendarAuditorio">
                Calendario de Auditorio
              </NavDropdown.Item>
              <NavDropdown.Item href="/bhs/about-us/classroom">
                Aulas Virtuales
              </NavDropdown.Item>
              <NavDropdown.Item href="/bhs/about-us/Calendarphotografic">
                Calendario Fotografico
              </NavDropdown.Item> */}
            </NavDropdown>
            <NavDropdown title="Explorar" id="collasible-nav-dropdown">
              <NavDropdown.Item href="/#/bhs/explorer/area">
                Area
              </NavDropdown.Item>
              <NavDropdown.Item href="/#/bhs/explorer/date">
                Periodo
              </NavDropdown.Item>
              <NavDropdown.Item href="/#/bhs/explorer/theme">
                Tema
              </NavDropdown.Item>
              <NavDropdown.Item href="/#/bhs/explorer/typearticle">
                Tipo de Articulo
              </NavDropdown.Item>
              <NavDropdown.Item href="/#/bhs/explorer/language">
                Idioma
              </NavDropdown.Item>
              <NavDropdown.Item href="/#/hsr/articles/collections">
                Colecciones
              </NavDropdown.Item>

            </NavDropdown>
            {adminIsValid && <NavDropdown title="Administrador" id="collasible-nav-dropdown">
            {/* <Nav.Link href="/hsr/articles/administrador">Administrador</Nav.Link> */}
            {/* <NavDropdown.Item href="/hsr/admArticles/administrador">
                Articulos
              </NavDropdown.Item> */}
              <NavDropdown.Item href="/#/hsr/admArticles/articles">
                Articulos
              </NavDropdown.Item>
              <NavDropdown.Item href="/#/hsr/admArticles/category">
              Categorias
              </NavDropdown.Item>
              <NavDropdown.Item href="/#/hsr/admarticles/areas">
              Areas
              </NavDropdown.Item>
              <NavDropdown.Item href="/#/hsr/admArticles/themes">
              Temas
              </NavDropdown.Item>
              {/* <NavDropdown.Item href="/hsr/admArticles/lenguages">
              Idiomas
              </NavDropdown.Item> */}
            </NavDropdown>}
          </Nav>
          <Nav>
            <Nav.Link eventKey={2} {...{ href: usuario ? "/#/" : "/#/login" }}>
              {usuario ? usuario.login_username : "Iniciar Sesion"}
            </Nav.Link>
            <Nav.Link eventKey={2} onClick={() => { logout() }}>
              {usuario && "Cerrar Sesion"}
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </NavBarStyled>
  );
};

export default NavbarX;
