import React, { Fragment, useEffect, useState } from "react";
import { Select, Form } from "react-bootstrap";
import DataTable from "react-data-table-component";
import useAxios from "hooks/useAxios";
import ItemArticleTable from "../../components/molecules/ItemArticleTable";
import { NoData, SearchStyled } from "./styled";
import { faCommentsDollar } from "@fortawesome/free-solid-svg-icons";
import { Button } from 'react-bootstrap';
import ModalGeneral from "../../pages/Modals/ModalGeneral";
import Axios from 'axios';
import { saveAs } from 'file-saver'


const Teaching = () => {
  const ListaDocencia = useAxios("GET", "/docencia/listar")
  const ListaDocenciaEspecialidades = useAxios("GET", "/DocenciaEspecialidades/listar")
  const lstUsersDocencia = useAxios("GET", `/usersSpecialties/list`)
  const lstUsersSpecialties = useAxios("GET", "/usersSpecialties/list")
  const list_evaluations = useAxios("GET", "/", null, false);

  const [id_docencia, setDocencia] = useState();
  const [id_EspDocencia, setEspDocencia] = useState();
  const [dataForm, setDataForm] = useState({})
  const [id_user, setIdUser] = useState(0)

  const [idModal, setIdModal] = useState(0)

  const [show, setShow] = useState(false);

  useEffect(() => {
    lstUsersDocencia.updateURL(`/usersSpecialties/list`)
    lstUsersDocencia.initRequest()

    return () => { };
  }, [])

  useEffect(() => {
    if (id_docencia && id_docencia !== "-1") {
      lstUsersDocencia.updateURL(`/usersSpecialties/list/${id_docencia}`)
      lstUsersDocencia.initRequest()
      ListaDocenciaEspecialidades.updateURL(`/DocenciaEspecialidades/listar/${id_docencia}`)
      ListaDocenciaEspecialidades.initRequest()
    } else {
      lstUsersDocencia.updateURL(`/usersSpecialties/list`)
      lstUsersDocencia.initRequest()
    }
    return () => { };
  }, [id_docencia])


  useEffect(() => {
    if(id_docencia && id_EspDocencia){
      lstUsersDocencia.updateURL(`/usersSpecialties/list/${id_docencia}/${id_EspDocencia}`);
      lstUsersDocencia.initRequest();
    }
    return () => { };
  }, [id_EspDocencia])

  useEffect(() => {
    if(id_user){
      // setIdUser(id_user);
      console.log(id_user);
      list_evaluations.updateURL(`/residents/evaluacionts/${id_user}`);
      list_evaluations.initRequest();
      console.log(list_evaluations);
    }
    return () => { };
  }, [id_user])


  useEffect(() => {
    list_evaluations.data.length > 0 && exportarPDF(list_evaluations.data);
    return () => { };
  }, [list_evaluations.data])

  // id de docencia
  const getUsuarioDocencia = (e) => {
    e.preventDefault();
    setDocencia(e.target.value)
  }
  // id de especialidad
  const getUsuEspDoc = (e) => {
    e.preventDefault();
    setEspDocencia(e.target.value)
    console.log("el valor del id de la especialidad es " + e.target.value);
  }

  const handleSave = (data) => {
    // const id_modal = 2;
    // setIdModal(2)
    setShow(true);
    setDataForm(data);
    // setDataForm("")

  }


  const handleReport = (data) => {

    
    setIdUser(data.ID_USUARIO);
    console.log(data.ID_USUARIO);
    console.log(id_user);
  
  }

    
  const exportarPDF=(data)=>{

    console.log(data);

    Axios.post(`http://192.168.1.23:5000/api/biblioteca/api/create-pdf`, {
      data: data,
      // console.log("entro a ")
    })
      .then(() => Axios.get(`http://192.168.1.23:5000/api/biblioteca/api/fetch-pdf`, {
        responseType: "blob",
      })

      )
      .then((res) => {
        Axios.get(`http://192.168.1.23:5000/api/biblioteca/api/remove-pdf`)
        const pdfBlob = new Blob([res.data], { type: "application/pdf" });
        saveAs(
          pdfBlob
        );
        data.setLoading && data.setLoading(false)
      })
      .catch(() => data.setLoading && data.setLoading(false))
      // console.log(list_evaluations);
  }

  

  return (
    <Fragment>

      <div >
        <Form.Select size="sm" onChange={getUsuarioDocencia}>
          <option value="-1">Seleccionar</option>
          {
            ListaDocencia.data.map(el => <option value={el.ID_DOCENCIA}>{el.DESCRIPTION_DOCEN}</option>)
          }
        </Form.Select>
        {/* <br /> */}
        <Form.Select size="sm" onChange={getUsuEspDoc}>
          <option value="-1">Seleccionar</option>
          {
            ListaDocenciaEspecialidades.data.map(el => <option value={el.ID_ESPECIALIDAD}>{el.DESCRIPTION_ESPEC}</option>)
          }
        </Form.Select>

      </div>
      <SearchStyled>
        <DataTable
          noDataComponent={<NoData><span>No se encontró ningun resultados.</span></NoData>}
          columns={[
            {
              name: "USUARIO", cell: (el) =>
                <el className="data NOMBRE-COMPLETOS">{el.ID_USUARIO}</el>

            },
            {
              name: "NOMBRE COMPLETOS", cell: (el) =>
                <el className="data NOMBRE-COMPLETOS">{el.NOMBRE_COMPLETO}</el>

            },
            {
              name: "DNI", cell: (el) =>
                <el className="data NOMBRE">{el.DNI}</el>

            },
            // {
            //   name: "ID_DOCENCIA", cell: (el) =>
            //     <el className="data ">{el.ID_DOCENCIA}</el>

            // },

            {
              name: "DESCRIPTION", cell: (el) =>
                <el className="data ">{el.DESCRIPTION_DOCEN}</el>

            },
            // {
            //   name: "ID_ESPECIALIDAD", cell: (el) =>
            //     <el className="data ">{el.ID_ESPECIALIDAD}</el>

            // },
            {
              name: "DESCRIPTION", cell: (el) =>
                <el className="data NOMBRE">{el.DESCRIPTION_ESPEC}</el>

            },



            {
              name: "ACCIÓN", cell: (el) => <Fragment>
                <div>
                  <Button className="btn-addnote" type="button" onClick={() => handleSave(el)}>
                    {/* AGREGAR */}
                    <i  class="fas fa-plus size:3x"></i>
                  </Button>
                </div>
                <div >
                  <Button className="btn-rept" type="button" onClick={() => handleReport(el)}>
                   
                    <i class="far fa-file-pdf"></i>
                  </Button>
                </div>
              </Fragment>
            },
          ]}
          pagination
          data={lstUsersDocencia.data}
        />
      </SearchStyled>

      {/* {console.log("el modal es "+ id_modal)} */}
      <ModalGeneral show={show} setShow={setShow} id_modal={idModal} data={dataForm}></ModalGeneral>



      {/* <ModalGeneral  show={show} setShow={setShow} id_modal={id_modal} data={dataForm}></ModalGeneral> */}
      {/* < TableEvaluaciones/> */}
    </Fragment>

  );

}
export default Teaching;