import { ExplorerListArticlesStyled } from "./styled";
import ArticlePreview from "components/molecules/ArticlePreview";

const ExplorerListArticles = ({ articles }) => {
  return (
    <ExplorerListArticlesStyled>
      {articles.map((article, key) => (
        <ArticlePreview data={article} />
      ))}
    </ExplorerListArticlesStyled>
  );
};

export default ExplorerListArticles;
