import { useEffect } from "react";
import Search from "components/molecules/Search/Search";
import useAxios from "hooks/useAxios";

const SearchLenguages = (props) => {
  const lenguages = useAxios("GET", "/", null, false);
console.log(props.match.params.id);

  useEffect(() => {
    var id_lenguage = props.match.params.id;

    lenguages.updateURL(`/articulos/por/lenguages/${id_lenguage}`);
    lenguages.initRequest();
    return () => {};
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <div>
      <Search
        data={lenguages.data}
        mensaje="Hola mundo"
      />
      
    </div>
  );
};

export default SearchLenguages;