import React, { Fragment, useState, useEffect } from "react";
import Formulario from "components/organismis/Formulario";
import { FormRow } from "components/organismis/Formulario/styled";
import useAxios from "hooks/useAxios";
import { Form, FormControl, FormGroup, Button } from "react-bootstrap";
import Modal from "pages/Modal.js";
import axios from "axios";
import { set } from "date-fns/esm";
import { compareAsc, format } from 'date-fns'
import { isNull } from "util";


const AdminArticle = ({ Modalprueba, setModalprueba, id_article }) => {
  const { Label } = Form;
  const ListaAreas = useAxios("GET", "/areas/listarart")
  const ListaColeciones = useAxios("GET", "/articulo/coleccion/listar")
  const ListaTemas = useAxios("GET", "/articulo/temas/listar")
  const ListaTipos = useAxios("GET", "/articulo/tipos/listar")
  const ListaPeriodos = useAxios("GET", "/articulo/periodos/listar")
  const ListaCategorias = useAxios("GET", "/articulo/categorias/listar")
  const ListaIdiomas = useAxios("GET", "/articulo/lenguajes/listar")
  const API_CONECTION = useAxios("POST", "/articulos/insertar", null, false);
  const API_UPDATEARTICLE = useAxios("POST", "/articulos/updateArticle", null, false);
  const List_article = useAxios("GET", "/", null, false)
  // const List_article = useAxios("GET",`/articulo/consultar`,, false)

  // const List_article = useAxios("GET", `/articulo/consultar/${id_article}`);
  // // console.log(id_article);
  const [show, setShow] = useState(true);
  // const [IdArticle, setIdArticle] = useState(id_article);

  const [datos, setDatos] = useState({
    TITLE_ARTICLE: "",
    AUTHOR: "",
    CONTENT_ARTICLE: "",
    ID_TYPE: 0,
    ID_THEME: 0,
    ID_CATEGORY: 0,
    ID_AREA: 0,
    ID_COLLECTION: 0,
    ID_LENGUAGE: 0,
    ID_PERIOD: 0,
    DATE_CONTENT: "",
    ARTICLE_FEATURED:"",
    ROUTE_IMG: "",
    ROUTE_FILE: "",
    fileImage: null,
    fileFile: null
    // archivoNombre: '',
    // archivoUrl: ''

  })


  useEffect(() => {

    if (id_article != "") {
      List_article.updateURL(`/articulo/consultar/${id_article}`);
      List_article.initRequest();
    }
    return () => {
    }
  }, [])
  //----------------editar articulo-------------------------

  useEffect(() => {
    cargarEnDatos()
    return () => {
    }
  }, [List_article.data])


  const cargarEnDatos = () => {
    // // console.log(List_article.data)
    List_article.data.length > 0 && setDatos(List_article.data[0])
  }
  //---------------------------------------

  const InputChange = (e) => {
    // console.log(e.target.value)
    setDatos({
      ...datos,
      [e.target.name]: e.target.value,
    })
  }

  const imageChange = (event) => {

    setDatos({
      ...datos,
      //  [event.target.name]: event.target.files[0].name,
      fileImage: event.target.files[0],
      ROUTE_IMG: event.target.files[0].name
    })
    //}
  }

  const fileChange = (event) => {
 
    setDatos({
      ...datos,
      //  [event.target.name]: event.target.files[0].name,
      fileFile: event.target.files[0],
      ROUTE_FILE: event.target.files[0].name

    })
    //}
  }

  const Savedata = () => {

    //insertar

    // if (datos.ID_ARTICLE) {

    //   API_UPDATEARTICLE.updateParams(datos)
    //   API_UPDATEARTICLE.sendRequest()

    // } else {
    //   API_CONECTION.updateParams(datos)
    //   API_CONECTION.sendRequest()
    // }
    // setShow(true)
    // console.log(datos);

    if (datos.fileFile || datos.fileImage) {

      if(datos.fileFile && datos.fileImage){
        // console.log("fileFile-fileImage");

        const fdpdf = new FormData();
          const fdImg = new FormData();
  
          fdImg.append("ArticleImage", datos.fileImage,);
          fdpdf.append("ArticlePDF", datos.fileFile,);
  
          axios.post('http://biblioteca.hsantarosa.gob.pe/restfull/uploadPDF.php', fdpdf)
            .then((resPDF, err) => {
              if (err) return console(err)
              // console.log(resPDF.data)
              if (!resPDF.data.error) {
                axios.post('http://biblioteca.hsantarosa.gob.pe/restfull/uploadImg.php', fdImg)
                  .then((resIMG, err) => {
                    if (err) return console(err)
                    // console.log(resIMG.data)
                    if (!resIMG.data.error) {
  
                      if (datos.ID_ARTICLE) {
                        API_UPDATEARTICLE.updateParams({ ...datos, ROUTE_FILE: resPDF.data.url, ROUTE_IMG: resIMG.data.url })
                        API_UPDATEARTICLE.sendRequest()
                      }
                      else {
                        API_CONECTION.updateParams({ ...datos, ROUTE_FILE: resPDF.data.url, ROUTE_IMG: resIMG.data.url })
                        API_CONECTION.sendRequest()
                      }
                      
                    }
                  })
  
              }
            })
        }

      
      else {
        if (datos.fileFile) {
          // console.log("filefile")
          const fdpdf = new FormData();
          fdpdf.append("ArticlePDF", datos.fileFile,);
  
          axios.post('http://biblioteca.hsantarosa.gob.pe/restfull/uploadPDF.php', fdpdf)
            .then((resPDF, err) => {
              if (err) return console(err)
              // console.log(resPDF.data)
              if (!resPDF.data.error) {
  
                if (datos.ID_ARTICLE) {
                  API_UPDATEARTICLE.updateParams({ ...datos, ROUTE_FILE: resPDF.data.url })
                  API_UPDATEARTICLE.sendRequest()
                }
                else {
                  API_CONECTION.updateParams({ ...datos, ROUTE_FILE: resPDF.data.url })
                 API_CONECTION.sendRequest()
                 }
                
              }
            })
        }
        else {
          if (datos.fileImage) {
            const fdImg = new FormData();
            // console.log("fileImage");
            fdImg.append("ArticleImage", datos.fileImage,);            
            axios.post('http://biblioteca.hsantarosa.gob.pe/restfull/uploadImg.php', fdImg)
            .then((resIMG, err) => {
              if (err) return console(err)
              // console.log(resIMG.data)
              if (!resIMG.data.error) {

                if (datos.ID_ARTICLE) {
                  API_UPDATEARTICLE.updateParams({ ...datos, ROUTE_IMG: resIMG.data.url })
                  API_UPDATEARTICLE.sendRequest()
                }
                else {
                  API_CONECTION.updateParams({ ...datos, ROUTE_IMG: resIMG.data.url })
                  API_CONECTION.sendRequest()
                }
              }
            })
    
          }
        }
      }
    }
  
    else {
      if (datos.ID_ARTICLE) {
        // console.log("sin carga de files con id_article");
        API_UPDATEARTICLE.updateParams(datos)
        API_UPDATEARTICLE.sendRequest()
      }
    //    else {
    //     // console.log("sin carga de files");
    //     API_CONECTION.updateParams(datos)
    //    API_CONECTION.sendRequest()
    //  }

    }
  
    setShow(true)
    // console.log(datos)
  }



  return (
    <Fragment>
      <div>
        <Formulario eventodef={Savedata}>
          {/* <h3>NUEVO ARTICULO</h3> */}
          <h3>{id_article === "" ? "NUEVO ARTICULO" : "EDITAR ARTICULO"}</h3>
          <FormGroup>
            <Label for="title">Titulo</Label>
            <FormControl value={datos.TITLE_ARTICLE} id="title" name="TITLE_ARTICLE" onChange={InputChange} required />
          </FormGroup>
          <FormGroup>
            <Label for="content">Contenido</Label>
            <FormControl value={datos.CONTENT_ARTICLE} id="content" as="textarea" name="CONTENT_ARTICLE" onChange={InputChange} required />
          </FormGroup>
          <FormGroup>
            <Label for="author">Autor</Label>
            <FormControl value={datos.AUTHOR} id="author" name="AUTHOR" onChange={InputChange} required />
          </FormGroup>

          <FormRow>
            <FormGroup>
              <Label for="type">Tipo</Label>
              <Form.Select id="type" aria-Label="Default type" name="ID_TYPE" value={datos.ID_TYPE} onChange={InputChange} >
                <option value={0}>Seleccionar</option>
                {ListaTipos.data.map(el => <option value={el.ID_TYPE}>{el.DESCRIPTION_TYPE}</option>)}
              </Form.Select>
            </FormGroup>
            <FormGroup>
              <Label for="theme-e">Tema</Label>
              <Form.Select id="theme-e" aria-Label="Default select example" name="ID_THEME" value={datos.ID_THEME} onChange={InputChange} >

                <option value={0}>Seleccionar</option>
                {
                  ListaTemas.data.map(el => <option value={el.ID_THEME}>{el.DESCRIPTION_THEME}</option>)
                }
              </Form.Select>
            </FormGroup>
            <FormGroup>
              <Label for="category">Categoria</Label>
              <Form.Select id="category" aria-Label="Default select example" name="ID_CATEGORY" value={datos.ID_CATEGORY} onChange={InputChange}  >
                <option>Seleccionar</option>
                {ListaCategorias.data.map(el => <option value={el.ID_CATEGORY}>{el.DESCRIPTION_CATEGORY}</option>)}
              </Form.Select>
            </FormGroup>
          </FormRow>
          <FormRow>
            <FormGroup>
              <Label for="area-t">Area</Label>
              <Form.Select id="area-t" aria-Label="Default select example" name="ID_AREA" value={datos.ID_AREA} onChange={InputChange} >
                <option>Seleccionar</option>
                {
                  ListaAreas.data.map(el => <option value={el.ID_AREA}>{el.DESCRIPTION_AREA}</option>)
                }
              </Form.Select>
            </FormGroup>
            <FormGroup>
              <Label for="collection">Colleccion</Label>
              <Form.Select id="collection" aria-Label="Default select example" name="ID_COLLECTION" value={datos.ID_COLLECTION} onChange={InputChange} >
                <option>Seleccionar</option>
                {
                  ListaColeciones.data.map(el => <option value={el.ID_COLLECTION}>{el.DESCRIPTION_COLLECTION}</option>)
                }
              </Form.Select>
            </FormGroup>
          </FormRow>
          <FormRow>
            <FormGroup>
              <Label for="lenguage">Idioma</Label>
              <Form.Select id="period" aria-Label="Default select example" name="ID_LENGUAGE" value={datos.ID_LENGUAGE} onChange={InputChange}  >
                <option>Seleccionar</option>
                {
                  ListaIdiomas.data.map(el => <option value={el.ID_LENGUAGE}>{el.DESCRIPTION_LENGUAGE}</option>)
                }
              </Form.Select>
            </FormGroup>
            <FormGroup>
              <Label for="date">Fecha de contenido</Label>
              <FormControl id="date" type="date" name="DATE_CONTENT" value={datos.DATE_CONTENT} onChange={InputChange} />
            </FormGroup>
            <FormGroup>
              <Label for="period">Periodo</Label>
              <Form.Select id="period" aria-Label="Default select example" name="ID_PERIOD" value={datos.ID_PERIOD} onChange={InputChange}  >
                <option>Seleccionar</option>
                {
                  ListaPeriodos.data.map(el => <option value={el.ID_PERIOD}>{el.DESCRIPTION_PERIOD}</option>)
                }
              </Form.Select>
            </FormGroup>

            <FormGroup>
              <Label for="art-dest">Articulo Destacado</Label>
              <Form.Select id="period" aria-Label="Default select example" name="ARTICLE_FEATURED" value={datos.ARTICLE_FEATURED} onChange={InputChange}  >
                <option>0</option>
                <option>1</option>
              </Form.Select>
            </FormGroup>

          </FormRow>
          <FormRow>
            <FormGroup>
              <Label for="img">Imagen referencial</Label>
              <FormControl id="img" type="file" accept="image/*" name="ROUTE_IMG" onChange={imageChange} />
              {
                datos.ROUTE_IMG !== null ? (<img style={{ width: "40%" }} src={datos.ROUTE_IMG} alt={datos.ROUTE_IMG} title={datos.ROUTE_IMG} />) : null
              }

            </FormGroup>
            <FormGroup>
              <Label for="doc">Documento</Label>
              <FormControl id="doc" type="file" accept="application/pdf" name="ROUTE_FILE" onChange={fileChange} />
              <span>{datos.ROUTE_FILE}</span>
            </FormGroup>
          </FormRow>
        </Formulario>
      </div>

     
      {!Array.isArray(API_CONECTION.data) && 
      <Modal title="Confirmación" text={API_CONECTION.data} show={show} setShow={setShow}  onHide={() => setModalprueba(false)}></Modal>
      // <GetArticles/>
      }
      {!Array.isArray(API_UPDATEARTICLE.data) && <Modal title="Confirmación" text={API_UPDATEARTICLE.data} show={show} setShow={setShow} onHide={() => setModalprueba(false)}>
      </Modal>}
    </Fragment>

  );
};
export default AdminArticle;
