import { Link } from "react-router-dom";
import { Button } from "react-bootstrap";
import { Container } from "components/Styled";
import TitleSection from "components/atoms/TitleSection";
import ExplorerListArticles from "components/organismis/ExplorerListArticles";
import useAxios from "hooks/useAxios";

const ExplorerLenguage = () => {
  const artbylanguage = useAxios("GET", "/articulos/por/lenguage/listar");
  console.log(artbylanguage);
  return (
    <Container>
      {artbylanguage.data.map((articlesbylanguage, key) => (
        <div key={key}>
          {articlesbylanguage.ARTICLES.length > 0 && (
            <div>
              <TitleSection
                href={"/#/hsr/search/lenguages/" + articlesbylanguage.ID_LENGUAGE}>
                  
                {articlesbylanguage.DESCRIPTION_LENGUAGE}
              </TitleSection>
              <ExplorerListArticles articles={articlesbylanguage.ARTICLES} />
              <div className="btn-explorer">
                <Button variant="dark" size="sm">
                  <Link
                    to={"/hsr/search/lenguages/" + articlesbylanguage.ID_LENGUAGE}
                  >
                    <span className="ver-mas">Ver mas</span>
                  </Link>
                </Button>
              </div>
            </div>
          )}
        </div>
      ))}
    </Container>
  );
};

export default ExplorerLenguage;
