
import React, { Fragment, useState, useEffect } from "react";
import Formulario from "components/organismis/Formulario";
import { FormRow } from "components/organismis/Formulario/styled";
import useAxios from "hooks/useAxios";
import { Form, FormControl, FormGroup, Button, Col, Row } from "react-bootstrap";

import Modal from "pages/Modal.js";
import axios from "axios";
import { daysToWeeks } from "date-fns/esm";

const AdminResidents = ({ Modalprueba, setModalprueba, data }) => {
    const { Label } = Form;
    const [show, setShow] = useState(true);
    const [disabled, setDisable] = useState(true);
    const ROTATION_INSERT = useAxios("POST", "/rotation/insert", null, false);

    const [datos, setDatos] = useState({
        ID_USUARIO: 0,
        NOMBRE: "",
        DESCRIPTION_ESPEC: "",
        ID_ESPECIALIDAD: 0,
        SERVICIO: "",
        LUGAR_ROTACION: 0,
        NOTA_FINAL: 0,
        OBSERVACIONES: " ",
        DATE_START: "",
        DATE_END: "",
        ANNIO: ""

    })

    const [notes, setNotes] = useState({ NOTA_1: 0, NOTA_2: 0, NOTA_3: 0, NOTA_4: 0, NOTA_5: 0 })


    useEffect(() => {
        setDatos({ ...data })
        return () => {
        }
    }, [data])

    const sumar = () => {
        const _suma = Number(Number(notes.NOTA_1) + Number(notes.NOTA_2) + Number(notes.NOTA_3) + Number(notes.NOTA_4) + Number(notes.NOTA_5))
        setDatos({
            ...datos,
            NOTA_FINAL: _suma
        })
    }

    useEffect(() => {
        if (datos.NOMBRE !== '' && datos.DESCRIPTION_ESPEC !== '') sumar()

        return () => { }
    }, [notes])


    const InputChange = (e) => {

        setDatos({
            ...datos,
            [e.target.name]: e.target.value,
            // OBSERVACIONES: e.target.value ? e.target.value : "No hay data"

        })
    }


    //calculo de notas
    const calculateNote = async (e) => {
        setNotes({
            ...notes,
            [e.target.name]: e.target.value,
        })
        console.log(notes);
    }

    const savedatanotes = () => {
        console.log(datos);
        ROTATION_INSERT.updateParams({ ...datos })
        ROTATION_INSERT.sendRequest()

        setShow(true)

        // console.log(datos)
    }

    return (
        <Fragment>
            <div>
                <Formulario eventodef={savedatanotes}>
                    {/* <h3>NUEVO ARTICULO</h3> */}
                    <h3>EVALUACIÓN</h3>
                    <FormGroup>
                        <Label for="title">ALUMNO</Label>
                        <FormControl id="NOMBRE" name="NOMBRE" value={datos.NOMBRE_COMPLETO} disabled={disabled} onChange={InputChange} />
                    </FormGroup>
                    <FormGroup>

                        <Label for="title">ESPECIALIDAD</Label>
                        <FormControl id="DESCRIPTION_ESPEC" name="DESCRIPTION_ESPEC" value={datos.DESCRIPTION_ESPEC} disabled={disabled} onChange={InputChange} />

                    </FormGroup>
                    <FormGroup>
                        <Label for="author">SERVICIO</Label>
                        <FormControl id="author" name="SERVICIO" onChange={InputChange} />
                    </FormGroup>

                    <FormGroup>
                        <Label for="author">LUGAR DE ROTACIÓN</Label>
                        <FormControl id="author" name="LUGAR_ROTACION" onChange={InputChange} required />
                    </FormGroup>

                    <FormRow>
                        {/* rotacion */}

                        <FormGroup>
                            <Label for="date">Fecha de Inicio</Label>
                            <FormControl id="date" type="date" name="DATE_START" onChange={InputChange} required />
                        </FormGroup>
                        <FormGroup>
                            <Label for="date">Fecha de Fin</Label>
                            <FormControl id="date" type="date" name="DATE_END" onChange={InputChange} required />
                        </FormGroup>

                        <FormGroup>
                            <Label for="title">AÑO</Label>
                            <FormControl id="ANNIO" name="ANNIO" onChange={InputChange} required />
                        </FormGroup>
                    </FormRow>

                    <FormGroup as={Row} >
                        <Label column sm="5" for="title">CONOCIMIENTOS TEORICOS</Label>
                        <Col sm="2">
                            <FormControl type="number" id="title" name="NOTA_1" step="0.1" defaultValue={0} onBlur={calculateNote} required />
                        </Col>
                    </FormGroup>

                    <FormGroup as={Row} >
                        <Label column sm="5" for="title">HABILIDAD PROFESIONAL: CRITERIO CLINICO, INICIATIVA Y DESTREZAS</Label>
                        <Col sm="2">
                            <FormControl id="title" defaultValue={0} type="number" name="NOTA_2" step="0.1" onBlur={calculateNote} required />
                        </Col>
                    </FormGroup>
                    <FormGroup as={Row} >
                        <Label column sm="5" for="title">PRESENTACIÓN Y PUNTUALIDAD</Label>
                        <Col sm="2">
                            <FormControl id="title" defaultValue={0} type="number" name="NOTA_3" step="0.1" onBlur={calculateNote} required />
                        </Col>
                    </FormGroup>
                    <FormGroup as={Row}>
                        <Label column sm="5" for="title">RELACIONES HUMANAS, TRABAJO EN EQUIPO</Label>
                        <Col sm="2">
                            <FormControl id="title" defaultValue={0} type="number" name="NOTA_4" step="0.1" onBlur={calculateNote} required />
                        </Col>
                    </FormGroup>
                    <FormGroup as={Row}>
                        <Label column sm="5" for="title">RESPONSABILIDAD</Label>
                        <Col sm="2">
                            <FormControl id="title" defaultValue={0} type="number" name="NOTA_5" step="0.1" onBlur={calculateNote} required />
                        </Col>
                    </FormGroup>

                    <FormGroup as={Row}>
                        <Label column sm="5" for="title">TOTAL</Label>
                        <Col sm="2">
                            <FormControl value={datos.NOTA_FINAL} type="number" id="title" name="NOTA_FINAL" step="0.1" disabled={disabled} onChange={InputChange} />
                        </Col>
                    </FormGroup>

                    <FormRow>
                        <FormGroup>
                            <Label for="content">OBSERVACIONES</Label>
                            <FormControl id="content" as="textarea" defaultValue={""} name="OBSERVACIONES" onChange={InputChange} />
                        </FormGroup>
                    </FormRow>
                </Formulario>
            </div>

            {!Array.isArray(ROTATION_INSERT.data) &&
                <Modal title="Confirmación" text={ROTATION_INSERT.data} show={show} setShow={setShow} onHide={() => setModalprueba(false)}></Modal>

            }


        </Fragment>

    );
};
export default AdminResidents;
