import React, { Fragment, useEffect, useState } from "react";

import { Button, Modal } from "react-bootstrap";
import ModalsArticle from "./Modalsframes/modalsArticle";

const ModalComponent = ({ title, text, status, show, setShow, onHide }) => {
 const [re, setret] = useState(true);

  return (
    <Fragment>
      <Modal show={show} onHide={() => { setShow(false);  onHide(); }}>
        <Modal.Header closeButton>
          <Modal.Title>{title}</Modal.Title>
        </Modal.Header>
        <Modal.Body>{text}</Modal.Body>
      </Modal>
      );
    </Fragment>
  );

}
export default ModalComponent;