import { Fragment } from "react";
import ArticleFeatured from "components/molecules/ArticleFeatured/ArticleFeatured";
import Carousel from "components/organismis/Carousel/Carousel";
import TitleSection from "components/atoms/TitleSection";
import useAxios from "hooks/useAxios";

const ArticlesFeatured = () => {
  const articlesFeatured = useAxios("GET","/articulos/destacados")
  return (
    <Fragment>
      {/* <TitleSection href="/es/search/?featured_item=true"> */}
       <TitleSection href="/#/hsr/get/articlesfeatured"> 
        Artículos destacados
      </TitleSection>
      <Carousel data={articlesFeatured.data} itemBody={(el) => <ArticleFeatured data={el}/> } />
    </Fragment>
  );
}

export default ArticlesFeatured;
