import React, { useState } from "react";
import { Button } from "react-bootstrap";
import { DescriptionArticleStyled, DescriptionItemStyled, DetailsArticleStyled } from "./styled";
import Moment from 'react-moment';
import CompartirPost from "components/CompartirPost";
import { compareAsc, format } from 'date-fns'


// const { format } = require("date-fns");
const Item = ({ header, content }) => {
  return (
    // <div>
    <DescriptionItemStyled>
      {/* <div className="div-items-content"> */}
      <h4 className="title-item-h4">{header}</h4>
      <span className="title-item-span">{content}</span>
    </DescriptionItemStyled>
    // </div>
  )
}



const DetArticle = ({ data }) => {

  // const { blob, setblob } = useState(data.ROUTE_FILE);

  const blob = new Blob([data.ROUTE_FILE])
  const { fileDownloadUrl, SetfileDownloadUrl } = useState(
    URL.createObjectURL(blob)
  );


  const dowlandFiles = (event) => {

    SetfileDownloadUrl(
      // this.dofileDownload.click(),
      URL.revokeObjectURL(fileDownloadUrl),
      // this.setState({fileDownloadUrl: ""}),
    )
  }


  return (

    <div>
      <DescriptionArticleStyled>
        <img src={data.ROUTE_IMG} alt="" />
        <div className="content-article">
          <div className="content-btns" >
            {/* <Button  variant="dark" size="sm">
              <i className="fas fa-share-alt btn-icon"></i>
              Compartir
            </Button>*/}
            {/* opcion para descargar el pdf */}

            {/* <Button variant="dark" size="sm" onClick={dowlandFiles}>
              <i className="fas fa-s btn-icon"></i>
              Descargar
            </Button> */}
            <CompartirPost Url={data.ROUTE_FILE} Titulo={data.TITLE_ARTICLE} Resumen={data.CONTENT_ARTICLE} />
          </div>

          <h3>{data.TITLE_ARTICLE} </h3>
          <p>
            <strong>SINOPSIS: </strong>
            <br />
            {data.CONTENT_ARTICLE}
          </p>
          <Button className="btns-readarticle" href={data.ROUTE_FILE} target="_blank">
            Leer articulo completo
          </Button>
        </div>
      </DescriptionArticleStyled >

      {/* const { format } = require("date-fns"); */}
      < DetailsArticleStyled >
        {/* <div className="sec-details-article">  */}
        < div className="title-content-details-article" >
          DESCRIPCIÓN
        </div >
        {<Item header="AUTOR" content={data.AUTHOR} /> }
        <Item header="FECHA DE CREACION" content={format(new Date(data.DATE_CREATION), 'dd-MM-yyyy')} />
        {/* <Item header="FECHA DE CONTENIDO" content={format(new Date(data.DATE_CONTENT), 'dd-MM-yyyy')} /> */}
        <Item header="IDIOMA" content={data.DESCRIPTION_LENGUAGE} />
        <Item header="TEMA" content={data.DESCRIPTION_THEME} />
        <Item header="TIPO DE ARTICULO" content={data.DESCRIPTION_TYPE} />
        <Item header="COLECCIÓN" content={data.DESCRIPTION_COLLECTION} />
        <Item header="AREA" content={data.DESCRIPTION_AREA} />
        {/* </div> */}

      </DetailsArticleStyled >
    </div >
  );
};

export default DetArticle;
