import styled from "styled-components";
export const ArticleFeaturedStyled = styled.div`
  height: 300px;
  max-height: 300px;
  position: relative;
  margin: 20px;
  img {
    width: 100%;
    height: 280px;
    z-index: 0;
    object-fit: fill;
  }
  .cafi-name {
    /* width: 100px; */
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: black;
    text-align: center;
  }
`;

export const LastStyled = styled.div`
  width: 100%;
  padding: 1em 11%;
  text-align: center;
  .content-last-article {
    margin: 15px 0;
    display: flex;
    gap: 10px;
    align-items: center;
    flex: 1;
  }
  .content-last-article > div {
    min-width: 200px;
    padding: 10px;
    display: 0;
    flex-direction: column;
    gap: 20px;
  }
  .p-featured-last {
    background-color: white;
    padding: 20px 25px;
    color: #333;
    border-radius: 1.5em;
    word-break: break-all;
    overflow: hidden;
  }
  .content-last-article img {
    width: 100%;
    min-height: 200px;
    max-height: 200px;
  }

  .btn-article-last {
    color: #e8e7e6;
    background-color: #403342;
    border-color: #1a1e21;
    margin-top: 10px;
  }

  h5 {
    color: #403342;
  }

  @media only screen and (max-width: 414px) {
    /* body { */
    /* border: 1px solid black; */
    /* display: flex;
    flex-direction: column; */
    /* width:50% ;  */
    .p-featured-last {
      max-height: 220px;
    }
    .content-last-article {
      display: flex;
      flex-direction: column;
      /* gap: 10px; */
      align-items: center;
    }

    .content-last-article > div {
      /* min-width: 150px; */
      display: flex;
      flex-direction: column;
      /* gap: 20px; */
    }
    /* h5{
    color: #403342;
    font-size: 0.5rem;
  } */
  }

  @media only screen and (min-width: 415px) and (max-width: 1023px) {
    /* body { */
    /* border: 1px solid black; */
    margin: auto;
    .p-featured-last {
      max-height: 220px;
    }
    .content-last-article {
      display: flex;
      flex-direction: row;
      /* gap: 10px; */
      .btn-article-last {
        margin-top: 10px;
      }
    }

    .content-last-article > div {
      /* min-width: 150px;  */
      /* display: flex; */
      flex-direction: column;
      align-items: center;
      /* gap: 20px; */
    }
  }
`;
