import { Fragment } from "react";
import TitleSection from "components/atoms/TitleSection";
import { AreasFeaturedStyled } from "../AreasFeatured/styled";
import ArticlePreview from "components/molecules/ArticlePreview";
import useAxios from "hooks/useAxios";


const ArticlesAddRecently = () => {
  const articles = useAxios("GET", "/articulos/recientes");
  console.log("los articulos recientes son los siguientes "+articles);
  return (
    <Fragment>
      <TitleSection href="/#/hsr/get/articlesaddrecently">Articulos agregados recientemente</TitleSection>     
      <AreasFeaturedStyled>
        {articles.data.map((article, key) => (
          <ArticlePreview data={article} key={key} />
        ))}
      </AreasFeaturedStyled>
    </Fragment>
  );
};

export default ArticlesAddRecently;
