import { Link } from "react-router-dom";
import { Button } from "react-bootstrap";
import { Container } from "components/Styled";
import TitleSection from "components/atoms/TitleSection";
import ExplorerListArticles from "components/organismis/ExplorerListArticles";
import useAxios from "hooks/useAxios";

const ExplorerTypeArticle = () => {
  const artbytype = useAxios("GET", "/articulos/por/tipos/listar");

  return (
    <Container>
      {artbytype.data.map((articlesbytype, key) => (
        <div key={key}>
          {articlesbytype.ARTICLES.length > 0 && (
            <div>
              <TitleSection href={"/#/hsr/search/type/" + articlesbytype.ID_TYPE}>
                {articlesbytype.DESCRIPTION_TYPE}
              </TitleSection>
              <ExplorerListArticles articles={articlesbytype.ARTICLES} />
              <div className="btn-explorer">
                <Button variant="dark" size="sm">
                  <Link to={"/hsr/search/type/" + articlesbytype.ID_TYPE}>
                    <span className="ver-mas">Ver mas</span>
                  </Link>
                </Button>
              </div>
            </div>
          )}
        </div>
      ))}
    </Container>
  );
};

export default ExplorerTypeArticle;
