import TitleSection from "components/atoms/TitleSection";
import Carousel from "../Carousel/Carousel";
import Last from "components/molecules/ArticleFeatured/Last";
import useAxios from "hooks/useAxios";
import { Styled } from "./styled";

const ArticlesFeaturedLast = () => {
  const ArticlesFeaturedLast = useAxios("GET", "/articulos/destacados");
  return (
    <Styled>
      <TitleSection href="#">Artículos destacados</TitleSection>
      <Carousel
        data={ArticlesFeaturedLast.data}
        onlyOne
        disableAutoPlay
        showDots
        itemBody={(artFeaturedLast, key) => <Last  data={artFeaturedLast} />}
      />
    </Styled>
  );
};

export default ArticlesFeaturedLast;
