import styled from 'styled-components';

export const Styled = styled.div`
    display: flex;
    gap: 10px;
    width: 80%;
    margin: 3rem auto;
    > .Link-img {
        width: 40%;
    }
    img {
        width: 100%;
        height: 200px;
        border-radius: .25rem;
    }
    @media only screen and (max-width: 414px) {
        .Link-img {
            display: none;
        }
    }
    @media only screen and (min-width: 415px) and (max-width: 1023px ) {
        .Link-img {
            display: none;
        }
    }
`

export const ContentArticle = styled.div`
    flex: 1;
    text-align: center;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    gap: 10px;
    max-height: 210px;
    a {
        color:  #403544;
        font-size: 14px;
        font-weight: 900;
    }
    a:hover {
        color: #000;
    }
    p {
        width: 90%;
        margin: auto;
    }
`