import React from "react";
import { responsive, responsiveOnlyOne } from "./responsive";
import { CarouselStyled } from "./styled";
const Carousel = ({ data, itemBody, onlyOne = false, disableAutoPlay = false, showDots = false }) => {
  return (
    <CarouselStyled responsive={ onlyOne ? responsiveOnlyOne : responsive} autoPlay = {!disableAutoPlay} infinite showDots = {showDots}>
      {Array.isArray(data) && data.map(itemBody)}
    </CarouselStyled>
  );
};
export default Carousel;
