import React, { Fragment, useEffect, useState } from "react";
import { Button, Modal } from "react-bootstrap";

import AdminResidents from "pages/administrators/admin_residents.js"
import List_evaluations from "exports/list_evaluations/index.js";


const ModalGeneral = ({ title , show, setShow, children, id_modal , data}) => {

    const [fullscreen, setFullscreen] = useState(true);
    //  console.log(id_modal)

  return (
    <Fragment>
      <Modal show={show} fullscreen={fullscreen} onHide={() =>{setShow(false)}}>
        <Modal.Header closeButton>
          <Modal.Title>{title}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
              <AdminResidents setModalprueba={setShow} data={data}/> 
            {/* {id_modal=3? <List_evaluations setModalprueba={setShow} data={data}/>: console.log("Error al cargar el componente")} */}
        {/* {children} */}
        </Modal.Body>
      </Modal>
      );
    </Fragment>
  );

}

export default ModalGeneral;