import React, { Fragment, useEffect, useState } from "react";
import { Select, Form } from "react-bootstrap";
import DataTable from "react-data-table-component";
import useAxios from "hooks/useAxios";
 import { NoData, SearchStyled } from "./styled";



const GetLenguages = () => {
   
   const data_lenguages = useAxios("GET", "/articulos/lenguages/listar");
   console.log("los articulos son los siguientes "+data_lenguages);
  //  const data_lenguajes_selec=useAxios("GET", "/articulos/category/listaselect");
  
return (
  <Fragment>
    <SearchStyled> 
      <DataTable
        noDataComponent={<NoData><span>No se encontró ningun resultados.</span></NoData>}
        columns={[
          {
            name: "ID", cell: (el) =>
              <el className="data id_categoria">{el.ID_LENGUAGE}</el>
          }
          ,
          {
            name: "CATEGORIA", cell: (el) =>
              <el className="data descategoria">{el.DESCRIPTION_LENGUAGE}</el>

          },
        
        ]}
        pagination
        data={data_lenguages.data}   
      />
     </SearchStyled> 
  </Fragment>

);

}
export default GetLenguages;