import { Link } from "react-router-dom";
import { Button } from "react-bootstrap";
import { Container } from "components/Styled";
import TitleSection from "components/atoms/TitleSection";
import ExplorerListArticles from "components/organismis/ExplorerListArticles";
import useAxios from "hooks/useAxios";

const ExplorerFecha = () => {
  const artbydate = useAxios("GET", "/articulos/por/periodo/listar");
 
  return (
    <Container>
      {artbydate.data.map((articlesbydate, key) => (
        <div key={key}>
          {articlesbydate.ARTICLES.length > 0 && (
            <div>
              <TitleSection href={"/#/hsr/search/date/" + articlesbydate.ID_PERIOD}>
                {articlesbydate.DESCRIPTION_PERIOD}
              </TitleSection>
              <ExplorerListArticles articles={articlesbydate.ARTICLES} />             
              <div className="btn-explorer">
                <Button variant="dark" size="sm">
                  <Link to={"/hsr/search/date/" + articlesbydate.ID_PERIOD}>
                    <span className="ver-mas">Ver mas</span>
                  </Link>
                </Button>
              </div>
            </div>
          )}
        </div>
      ))}
    </Container>
  );
};

export default ExplorerFecha;
