import React, { Fragment, useEffect, useState } from "react";
import { Select, Form } from "react-bootstrap";
import DataTable from "react-data-table-component";
import useAxios from "hooks/useAxios";
import { NoData, SearchStyled } from "./styled";



const GetCategory = () => {
   
  const data_category = useAxios("GET", "/articulos/category/listar")
  
return (
  <Fragment>

    <SearchStyled> 
      <DataTable
        noDataComponent={<NoData><span>No se encontró ningun resultados.</span></NoData>}
        columns={[
          {
            name: "ID", cell: (el) =>
              <el className="data NOMBRE">{el.ID_CATEGORY}</el>
          }
          ,
          {
            name: "CATEGORIA", cell: (el) =>
              <el className="data NOMBRE">{el.DESCRIPTION_CATEGORY}</el>

          },
        
        ]}
        pagination
        data={data_category.data}   
      />
     </SearchStyled> 
  </Fragment>

);

}
export default GetCategory;