import { TitleSectionStyled } from "./styled"
import { TitleSectionStyledGeneral } from "./styled"

const TitleSection = ({ href, children }) => {
    return (
        <div >
            <TitleSectionStyledGeneral>

                <TitleSectionStyled href={href}>
                    <span>{children}</span>
                    <i className="fas fa-angle-right"></i>
                </TitleSectionStyled>

            </TitleSectionStyledGeneral>
        </div>
    )


}

export default TitleSection