import React, { Fragment } from "react";
import Toolbar from "components/molecules/Toolbar/Toolbar.js";
import ArticlesFeatured from "components/organismis/ArticlesFeatured/articles_featured.js";
import AreasFeatured from "components/organismis/AreasFeatured/areas_featured.js";
import ArticlesaddRecently from "components/organismis/ArticlesAddRecently/articles_add_recently.js";
import ArticlesFeaturedLast from "components/organismis/ArticlesFeaturedLast/articles_featured_last.js";
import { FlexContainer } from "components/Styled";


const Home = () => {
    return (
      <Fragment>
        <Toolbar /> {/* frente o portada de la pagina home */}
        <FlexContainer gap="10px" >
          <ArticlesFeatured />
          <ArticlesaddRecently />
          <AreasFeatured />
          <ArticlesFeaturedLast />
        </FlexContainer>
      </Fragment>
    );
}
//  exportando el componente
export default Home;
