import axios from "axios";
import { useEffect, useState } from "react";
import Global from "components/Global";
import AxiosTokenProvider from "axios-token-interceptor";
import { useUsuario } from "./ContextUser";
const { url } = Global
function useAxios(METHOD, URL, PARAMS = null, initialFlag = true, NotBaseUrl=false) {
  const { usuario } = useUsuario()
  const [data, setData] = useState([]);
  const [error, setError] = useState(null);
  const [responseCode, setResponseCode] = useState();
  const [loading, setLoading] = useState(false);

  const reset = () => {
    setResponseCode(null);
    setData([]);
    setError(null);
    setLoading(false);
  };

  const updateURL = (newURL) => {
    URL = newURL;
  };
  const updateParams = (newParams) => {
    PARAMS = {...newParams, idUsuario : usuario.idEmpleado, login_username: usuario.login_username };
  };

  const getInstance = () => {
    const instance = axios.create({ baseURL : NotBaseUrl ? "" : url })
    usuario && instance.interceptors.request.use(
      AxiosTokenProvider({
        getToken: () => usuario.store + " " + usuario.rol,
      })
    );
    return instance
  };

  const getRequest = async (instance) => {
    let request = null;
    switch (METHOD) {
      case "POST":
        request = await instance.post(URL, PARAMS);
        break;
      case "GET":
        request = await instance.get(URL);
        break;
      default:
        break;
    }
    return request;
  };

  const sendRequest = async () => {
    setLoading(true);
    try {
      const instance = getInstance();
      const response = await getRequest(instance);
      console.log(response);
      setResponseCode(200);
      setData(response ? response.data : []);
      setLoading(false);
    } catch (error) {
      setData([]);
      setResponseCode(500);
      setError(error);
      setLoading(false);
    }
  };

  useEffect(() => {
    if (initialFlag) {
      if(usuario){
        PARAMS = {...PARAMS, idUsuario : usuario.idEmpleado};
      }
      initRequest();
    }
    return () => {
      axios.Cancel();
    };
    //eslint-disable-next-line
  }, [initialFlag]);

  const initRequest = () => {
    reset();
    sendRequest();
  };

  return {
    data,
    loading,
    error,
    responseCode,
    setData,
    updateURL,
    updateParams,
    initRequest,
    sendRequest,
    reset,
  };
}

export default useAxios;
