import { Fragment, useEffect, useState } from "react";
import "assets/styles/App.css";
import Explorer from "router/router_explorer";
import Footer from "components/molecules/footer/Footer.js";
import Navbar from "components/molecules/navbar/Navbar.js";
import Home from "pages/Home.js";
import { Route, Redirect } from "react-router-dom";
import Description_article from "pages/Description_article";
// import Search_article from "components/molecules/Search";
import Search_areas from "pages/searchs/Search_areas";
import Search_date from "pages/searchs/Search_date";
import Search_theme from "pages/searchs/Search_date";
import Search_type from "pages/searchs/Search_type";
import Search_lenguages from "pages/searchs/Search_lenguages";
import ExplorerCollections from "pages/explorer/explorer_collections";
import Search_General from "pages/searchs/Search_General";
import Search_articles_add_recently from "pages/searchs/Search_articles_add_recently";
import Search_article_featured from "pages/searchs/Search_article_featured";
import Search_collections from "pages/searchs/Search_collections";
import Explorer_areas from "pages/explorer/explorer_areas.js";
import Admin_article from "pages/administrators/admin_article.js";
import About from "pages/about-us/index.js";
import Organigrama from "pages/organigrama/index.js";
import Teaching from "pages/teaching/index.js";
import CalendarAudience from "pages/calendarAudience/index.js";
import getarticle from "pages/frm_list/getArticle/index.js";
import getcategory from "pages/frm_list/getCategory/index.js";
import getAreas from "pages/frm_list/getAreas/index.js";
import getTheme from "pages/frm_list/getTheme/index.js";
import getLenguages from "pages/frm_list/getLenguages/index.js";


import { useHistory } from 'react-router-dom';
// import { BrowserRouter,Router, Route, Switch } from "react-router";
import { Switch } from "react-router-dom";
import Login from "pages/Login";
import { useUsuario } from "hooks/ContextUser";
import Page403 from "pages/page403";

const Layout = () => {
  const { usuario, initVal } = useUsuario();
  const [adminIsValid, setAdminIsValid] = useState(false);

  useEffect(() => {
    setAdminIsValid(usuario && (usuario.rol === 36 || usuario.rol === 9))
    return () => { }
  }, [usuario])

  const { push } = useHistory()
  useEffect(() => {
    !initVal && !usuario && push('/login')
    return () => { }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [usuario])

  return (
    <Fragment>
      <Navbar />
      {/* <Router> */}
      <Switch>
        <Route exact path="/Login">
          {usuario ? <Redirect to="/" /> : <Login />}
        </Route>

        {usuario && <Fragment>
          <Route exact path="/" component={Home} />
          <Route exact path="/home" component={Home} />
          <Route exact path="/hsr/article/:id" component={Description_article} />
          <Route exact path="/hsr/about-us/about" component={About} />

          {/* router celene v29112021 */}
          <Route exact path="/hsr/about-us/Organigrama" component={Organigrama} />
          <Route exact path="/hsr/about-us/teaching" component={Teaching} />
          <Route exact path="/hsr/about-us/calendarAudience" component={CalendarAudience} />
          <Route exact path="/hsr/admArticles/articles" component={adminIsValid ? getarticle : Page403} />
          <Route exact path="/hsr/admArticles/category" component={adminIsValid ? getcategory : Page403} />
          <Route exact path="/hsr/admarticles/areas" component={adminIsValid ? getAreas : Page403} />
          <Route exact path="/hsr/admArticles/themes" component={adminIsValid ? getTheme : Page403} />
          <Route exact path="/hsr/admArticles/lenguages" component={adminIsValid ? getLenguages : Page403} />

          <Route exact path="/hsr/search/area/:id" component={Search_areas} />
          <Route exact path="/hsr/search/date/:id" component={Search_date} />
          <Route exact path="/hsr/search/theme/:id" component={Search_theme} />
          <Route exact path="/hsr/search/type/:id" component={Search_type} />
          <Route exact path="/hsr/search/lenguages/:id" component={Search_lenguages} />
          <Route exact path="/hsr/search/collections/:id" component={Search_collections} />

          <Route
            exact
            path="/hsr/articles/collections"
            component={ExplorerCollections}
          />
          <Route
            exact path="/hsr/admArticles/administrador"
            component={adminIsValid ? Admin_article : Page403}
          />
          {/* busqueda general para el toolbar */}
          <Route exact path="/hsr/search/:idgeneral" component={Search_General} />
          <Route
            exact
            path="/hsr/get/articlesaddrecently"
            component={Search_articles_add_recently}
          />
          <Route exact path="/hsr/get/areasfeatured" component={Explorer_areas} />
          <Route
            exact
            path="/hsr/get/articlesfeatured"
            component={Search_article_featured}
          />
          {/* mostrar ventana de confirmacion */}
          {/* <Route exact path="/hsr/get/mostrarconfirmacion" component={mostrar_confirmacion} /> */}
          <Explorer />
          {/* ruta para buscar un articulo */}
        </Fragment>}
      </Switch>
      {/* </Router> */}
      <Footer />
    </Fragment>
  );
};

export default Layout;
