import { Container } from "components/Styled";
import styled from "styled-components";
const bxsdow = "0 1px 1px rgba(229, 103, 23, 0.075) inset, 0 0 4px #403342"
export const SearchStyled = styled(Container)`
  .form-search {
    display: flex;
    width: 60%;
    margin: auto;
    height: 120px;
    align-items: center;
  }
  .frm_input_search{
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
  }
  .frm_search {
    border-bottom-left-radius: 0;
    border-top-left-radius: 0;
  }


  .frm_input_search:focus {
    box-shadow: ${bxsdow};
  }
  .frm_input_search:active{
      outline: none;
  }

  .btnAdd {
    display: flex;
    padding-top: 20px;
    padding-bottom: 20px;
    align-content: flex-end
     /* margin: auto;  */
    /* align-items: flex-end; */
    /* text-align: right; */
  }


  .btn-editart{
    margin-bottom: 5px;
  }
`;


export const NoData = styled.div`
    padding: 1rem;
`