import styled from 'styled-components';
export const TitleSectionStyled = styled.a`
    font-size: 20px;
    display: flex;
    gap: 10px;
    align-items: center;
    color: rgb(13, 73, 112);
    color: #403342;
    font-weight: bold; 
   

    :hover,:focus {
        color: #403342;

        
    }
    span {
        font-weight: normal;
        letter-spacing: .25px;
        
    }
    i {
        font-size: 28px;
        font-weight: bold;
        
    }
`

export const TitleSectionStyledGeneral = styled.div`
margin-bottom:  5px;
margin-top: 20px;  

`