import styled from "styled-components";

export const DescriptionItemStyled = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: 1px ridge #0000003b;
  align-items: flex-start;
  margin-top: 15px;
  margin-bottom: 15px;

  .title-item-h4 {
    font-size: 15px;
  }
  .title-item-span {
    font-size: 12px;
  }
`;

export const DescriptionArticleStyled = styled.div`
  display: flex;
  gap: 20px;
  margin-bottom: 25px;

  img {
    width: 300px;
    height: 300px;
    object-fit: contain;
    border: solid 1px #c3c3c3;
    padding: 5px;
    border-radius: 5px;
  }
  .content-article {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    gap: 10px;
  }
  .content-article > h3 {
    align-self: flex-start;
    /* color:  #66323f ; */
    font-size: 23px;
  }
  .content-article p {
    text-align: center;
    word-break: break-all;
  }
  .content-article > div {
    align-self: flex-end;
    display: flex;
    gap: 10px;
  }

  .content-btns > Button {
    color: #e8e7e6;
    background-color: #403342;
  }

  .btn-primary {
    color: #e8e7e6;
    background-color: #403342;
    border-color: none;
  }

  .btns-readarticle {
    border-color: #1a1e21;
  }

  @media only screen and (max-width: 414px) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    gap: 10px;
    /* width: 50%;  */

    .content-btns {
      align-self: flex-start;
      justify-content: left;
      align-content: left;
      align-items: left;
    }
    .h3 {
      justify-content: center;
      align-content: center;
    }
  }

  @media only screen and (min-width: 415px) and (max-width: 1023px) {
    display: flex;
    flex-direction: column;
    align-items: center;

    .content-btns {
      flex-direction: row;
    }

    .content-article > h3 {
      /* align-self: flex-start; */
      /* color:  #66323f ; */
      font-size: 23px;
      justify-content: center;
      align-content: center;
      align-items: center;
    }
  }
`;

export const DetailsArticleStyled = styled.div`
  /* display: flex;
    gap: 20px; */
  margin-top: 21px;
  margin: auto;
  width: 50%;
  /* text-align: center; */

  /* padding-right: 25px; */
  justify-content: center;
  align-content: center;
  align-items: center;

  .title-content-details-article {
    /* color:  #66323f ; */
    padding-top: 15px;
    padding-bottom: 15px;
    font-size: 18px;
    border: 1px ridge #0000003b;
    padding-left: 10px;
    width: 100%;
    border-radius: 5px;
  }

  @media only screen and (max-width: 414px) {
    width: 100%;
  }

  @media only screen and (min-width: 415px) and (max-width: 1023px) {
    /* display: flex;
flex-direction: column; */
    /* align-items: center;  */
    width: 100%;

    .content-btns {
      flex-direction: row;
    }
  }
`;
