import styled from 'styled-components';
import { Link } from 'react-router-dom';
export const AreaFeaturedStyled = styled(Link)`
    background-color: #f5f5f5;
    border: solid 1px #c3c3c3;
    padding: 1em;
    border-radius: .3em;
    text-align: center;
    min-width: 250px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    transition: all .5s;
    :hover {
        transform: scale(1.020);
    }
    img{ 
        width: 200px;
        height: 200px;
        border-radius: 0.2em;
        margin: auto;
    }
    a {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        color: black;
    }
    h6 {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        color: black;
    }

    .counter {
        background-color: #403342;
        width: max-content;
        margin: auto;
        padding: .3em 1em;
        border-radius: 1em .25em 1em .25em;
        color: white;

    }
`