import React from 'react'
import { ArticleFeaturedStyled } from './styled'

const ArticleFeatured = ({data }) => {
    return (
        <ArticleFeaturedStyled>
            <a
                href={"/#/hsr/article/" + data.ID_ARTICLE}
                title={data.TITLE_ARTICLE}
            >
                <img alt="..." src={data.ROUTE_IMG} />
                <div className="cafi-name">
                    {data.TITLE_ARTICLE}
                </div>
            </a>
        </ArticleFeaturedStyled>
    )
}

export default ArticleFeatured
