import { Row, Col, Form, Button } from "react-bootstrap";
import DataTable from "react-data-table-component";
import { useHistory } from "react-router";
import { useState } from "react";
import ItemArticleTable from "../ItemArticleTable";
import { NoData, SearchStyled } from "./styled";

// var des_search=this.props.match.params.IdArea;

const Search_article = (props) => {
  const { push } = useHistory();
  const [value, setValue] = useState("");

  return (
    <SearchStyled>
      <Form className="form-search">
        <Form.Control
          value={value}
          onChange={(e) => setValue(e.target.value)}
          className="frm_input_search"
          placeholder="Buscar articulos"
        />

        <Button
          className="frm_search btn-ajus"
          onClick={() => push("/hsr/search/" + value)}
          type="submit">
          Buscar
        </Button>
      </Form>
      <DataTable
        noDataComponent={<NoData><span>No se encontró ningun resultados.</span></NoData>}
        columns={[
          { name: "RESULTADOS", cell: (el) => <ItemArticleTable data={el} /> },
        ]}
        pagination
        data={props.data}
      />
    </SearchStyled>
  );
};

export default Search_article;
