import { Link } from "react-router-dom";
import { Button } from "react-bootstrap";
import { Container } from "components/Styled";
import TitleSection from "components/atoms/TitleSection";
import ExplorerListArticles from "components/organismis/ExplorerListArticles";
import useAxios from "hooks/useAxios";

const ExplorerCollections = () => {
  const artbycollections = useAxios("GET", "/articulos/por/colleccion/listar");

  return (
    <Container>
      {artbycollections.data.map((articlesbycollections, key) => (
        <div key={key}>
          {articlesbycollections.ARTICLES.length > 0 && (
            <div>
              <TitleSection
                href={
                  "/#/hsr/search/collections/" +
                  articlesbycollections.ID_COLLECTION
                }
              >
                {articlesbycollections.DESCRIPTION_COLLECTION}
              </TitleSection>
              <ExplorerListArticles articles={articlesbycollections.ARTICLES} />
              <div className="btn-explorer">
                <Button variant="dark" size="sm">
                  <Link to={"/hsr/search/collections/" + articlesbycollections.ID_COLLECTION}>
                    <span className="ver-mas">Ver mas</span>
                  </Link>
                </Button>
              </div>
            </div>
          )}
        </div>
      ))}
    </Container>
  );
};

export default ExplorerCollections;
