import { FormularioStyled } from "./styled"
import { Button } from 'react-bootstrap';
import { Fragment, useState } from "react";
import useAxios from "hooks/useAxios";
import Axios from 'axios'
import ModalAccept from "pages/Modal.js";


const Formulario = ({ children, eventodef}) => {

    return (
        <FormularioStyled  onSubmit={(e) => {
            e.preventDefault();
            eventodef();
        }}>
            {children}
            <div className="foot-form">
                <Button type="submit">
                    Guardar
                </Button>
            </div>
            {/*<div>
                <ModalAccept status={false} />
            </div>*/}
        </FormularioStyled>
    )



}

export default Formulario
