// // import Explorer from 'components/Home/body/explorer/Explorer.js';
import "assets/styles/explorer.css";
import { Link } from "react-router-dom";
import { Button } from "react-bootstrap";
import { Container } from "components/Styled";
import TitleSection from "components/atoms/TitleSection";
import ExplorerListArticles from "components/organismis/ExplorerListArticles";
import useAxios from "hooks/useAxios";

const ExplorerTheme = () => {
  const artbytheme = useAxios("GET", "/articulos/por/temas/listar");
  return (
    <Container>
      {artbytheme.data.map((articlesbytheme, key) => (
        <div key={key}>
          {articlesbytheme.ARTICLES.length > 0 && (
            <div>
              <TitleSection href={"/#/hsr/search/theme/" + articlesbytheme.ID_THEME}>
                {articlesbytheme.DESCRIPTION_THEME}
              </TitleSection>
              <ExplorerListArticles articles={articlesbytheme.ARTICLES} />
              <div className="btn-explorer">
                <Button variant="dark" size="sm">
                  <Link to={"/hsr/search/theme/" + articlesbytheme.ID_THEME}>
                    <span className="ver-mas">Ver mas</span>
                  </Link>
                </Button>
              </div>
            </div>
          )}
        </div>
      ))}
    </Container>
  );
};

export default ExplorerTheme;