import { useEffect } from "react";
import { Container } from "components/Styled";
import DetArticle from "components/molecules/DetArticle";
import useAxios from "hooks/useAxios";

const DescriptionArticle = (props) => {
  const DetalleArticle = useAxios("GET","/",null, false)

  useEffect(() => {
    let id_article = props.match.params.id;
    DetalleArticle.updateURL(`/articulo/consultar/${id_article}`);
    DetalleArticle.initRequest();
    return () => {}
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.match.params.id])

  useEffect(() => console.log(DetalleArticle.data), DetalleArticle.data)
  
  return (
    <Container>
      {Array.isArray(DetalleArticle.data) && DetalleArticle.data.length > 0 && <DetArticle data={DetalleArticle.data[0]}/>}      
    </Container>
  );

}
export default DescriptionArticle;
